.events {
	display: flex;
	flex-direction: row;

	&__item {
		width: 50%;
	}

	&__main {
		padding: 2.4rem;
	}

	&__map {
		position: relative;
		height: 50vh;
		&:before {
			position: absolute;
			z-index: 1;
			top: 0;
			left: 0;
			display: block;
			width: 100%;
			padding: 0	 4.8rem;
			content: "карта подій";
			text-transform: uppercase;
			color: $darkGrey;
			background: rgba($white, .85);
			font-size: 2.4rem;
			line-height: 6.5rem;
		}
	}
}
