.main-content{
	&__form{
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-around;
		width: 50%;
		height: 30%;
		border-bottom: 6px solid #efefef;
	form{
	position: relative;
	width: 85%;
	img{
	position: absolute;
	top: 0;
	right: 10px;
	cursor: pointer;
		}
	}
	hr {
		height: 1px;
		border: none;
		background: #b0b0b0;
	}
}

input[type=file]{
	display: none;
	}

	input[type=text]{
	width: 80%;
	padding-bottom: 5px;
	font-size: 14px;
	color: #b0b0b0;
	outline: none;
	border: none;
}

input[type=submit]{
	display: block;
	float: right;
	margin-top: 5px;
	padding: 3px 12px;
	background: #176997;
	color: #fff;
	font-size: 13px;
	text-transform: uppercase;
	border: none;
	cursor: pointer;
	outline: none;
	&:active{
		background: #ccc;
		}
	}

	&__photo{
	@extend .main-content__form;
	justify-content: flex-start;
	overflow: auto;
	img{
		display: none;
		float: right;
		margin: 5px 5px 0 0;
	}
	&__1{
		width: 120px;
		height: 90px;
		margin: 5px;
		background: #1E9CE8;
		&:hover img{
			display: block;
		}
	}
	&__2{
		width: 120px;
		height: 90px;
		margin: 5px;
		background: #B4BD4D;
		&:hover img{
			display: block;
		}
	}
	&__3{
		width: 120px;
		height: 90px;
		margin: 5px;
		background: #92ABBD;
		&:hover img{
			display: block;
			}
		}
	}
}

.photo-user{
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	width: 100%;
	height: calc(100vh - 30% - 12.8rem);
	overflow: auto;
	&__main{
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		position: relative;
		width: 45%;
		height: 90%;
		margin: 20px;
		box-shadow: 0 5px 5px #ccc;
		span{
			padding-left: 15px;
			font-size: 14px;
			color: #b0b0b0;
			&:active{
				color: #ccc;
			}
		}
		a{
			text-decoration: none;
		}
	}
	&__photo{
		width: 85px;
		height: 100px;
		border: 1px solid #999;
		border-radius: 4px;
		background: url(../img/participant-photo.png);
		background-size: cover;
	}
	&__name{
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100px;
		padding-left: 20px;
		h3{
			font-size: 18px;
			font-weight: 400;
			color: #5FAFB4
		}
		span{
		padding-top: 5px;
		font-size: 13px;
		font-weight: 400;
		color: #666;
		}
	}
	&__content{
		height: 25%;
		overflow: auto;
		p{
			padding: 15px 15px 5px 15px;
			font-size: 13px;
			color: #333;
		}
	}
	&__button{
		position: absolute;
		bottom: 20px;
		right: 30px;
		button{
			border: none;
			background: transparent;
			text-transform: uppercase;
			margin-left: 20px;
			font-size: 12px;
			font-weight: 500;
			color: #b0b0b0;
			outline: none;
			cursor: pointer;
			&:hover{
				color: #176997;
			}
			&:active{
				color: #ccc;
			}
		}
	}
}

@media screen and (max-width: 1200px){
.photo-user{
	&__main{
		width: 100%;
		}
	}
.main-content__photo{
	justify-content: center;
	}
}

@media screen and (max-width: 1000px){
.main-content{
	&__form{
		width: 100%;
		height: 20%;
		}
	}

.main-content__photo{
	justify-content: flex-start;
	}
}

@media screen and (max-width: 500px){
.main-content__photo{
	justify-content: center;
	}

.photo-user__name{
	margin-left: 5px;
	h3{
		font-size: 11px;
	}
span{
	font-size: 8px;
		}
	}
.photo-user__main{
	margin: 10px;
	span{
		font-size: 9px;
	}
}
.photo-user__content {
	p {
		padding: 5px;
		font-size: 10px;
	}
}

.photo-user__button{
	button{
		font-size: 7px;
	}
}

.main-content{
	input[type=text]{
		font-size: 10px;
	}
	input[type=submit] {
		font-size: 8px;
	}
}

.main-content__form{
	form{
	img{
		right: 0;
			}
		}
	}
}

@media screen and (max-width: 400px){
.photo-user{
	&__main {
	justify-content: center;
	}
	&__name{
		height: 50px;
		padding: 0;
		}
	}

.photo-user__button{
	button{
		font-size: 6px;
		}
	}
}

@media screen and (max-width: 360px){
.photo-user__button{
	button{
		font-size: 4px;
		}
	}
}



