.item {
	position: relative;
	overflow: hidden;
	margin-top: 1.5rem;
	padding: 0 17rem 0 15.3rem;
	box-shadow: 2px 4px 5px 0 rgba($black, .13);

	&__img {
		position: absolute;
		left: 0;
	}
	&__inner {
		padding: 2rem 2rem 3.2rem;
	}

	&__title {
		margin-bottom: 1.5rem;
		color: $black;
		font-size: 2.4rem;
		font-weight: 400;
	}
	&__type {
		margin-bottom: 1rem;
		color: #7a7979;
		font-size: 1.8rem;
		font-style: italic;
	}
	&__descr {
		color: #7b7979;
		font-size: 1.8rem;
		font-style: italic;
	}
	&__label {
		position: absolute;
		top: 2rem;
		right: 2rem;
		width: 6.4rem;
		text-align: center;
		color: $white;
		background: $blue;
		font-size: 2.4rem;
		font-weight: 300;
		line-height: 4rem;
		&--negative {
			background: $crimson;
		}
	}
	&__btn {
		padding: 0 4rem 0 1.8rem;

		&:before {
			@include btn-triagnle(transparent, transparent, transparent, $orange);
			border-width: 7px 0 7px 10px;
			right: 1rem;
			margin-top: -.7rem;
		}
	}
}
