.sidebar {
	position: fixed;
	width: 12.8rem;
	height: 100vh;
	background: $blue;

	&__logo {
		height: 12.8rem;
		text-align: center;
		color: $white;
		background: $darkBlue;
		font-size: 6.2rem;
		font-weight: 300;
		line-height: 12.8rem;
	}

	&__button {
		display: block;
		height: 12.8rem;
		background: url("./../img/sidebar__button.png") no-repeat center;
		transition: all .17 easy;
		&:hover {
			background-color: $darkBlue;
		}
	};

	&__photo-wrap {
		height: 12.8rem;
		padding: 2rem 0;
		text-align: center;
	}

	&__photo {
		border: 3px solid $white;
		border-radius: 50%;
	}
}
