.user-info {
	position: relative;
	padding: 2rem;
	color: #ececec;
	background-color: rgba(0,0,0, .6);
	font-weight: 300;
	overflow: hidden;

	&:before {
		position: absolute;
		z-index: -1;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		content: "";
		background: url("./../img/user-info-bg.png") no-repeat 50% 50%;
		background-size: cover;
		filter: blur(1rem);
	}

	&__avatar-wrapper {
		position: relative;
		width: 23rem;
		height: 23rem;
		margin: 5.6rem auto 3rem;
		border: .4rem solid;
		border-radius: 50%;
	}

	&__rating {
		position: absolute;
		top: .5rem;
		right: .5rem;
		display: block;
		width: 5.6rem;
		height: 5.6rem;
		text-align: center;
		border-radius: 50%;
		background: $blue;
		font-size: 1.8rem;
		line-height: 5.6rem;
	}

	&__name {
		text-align: center;
		color: $whiteGrey;
		font-size: 4.8rem;
		font-weight: 300;
	}

	&__descr {
		margin-bottom: .4rem;
		position: relative;
		left: 20%;
		font-size: 2.4rem;
		line-height: 4.8rem;

		& > span {
			padding: 0 .8rem;
		}

		p {
			font-size: 1.8rem;
			line-height: 2.4rem;
		}
	}

	@at-root span.user-info__city {
		padding-left: 0;
	}

	&__age,
	&__city {
		border-right: 1px solid;
	}

	&__social {
		margin-top: 3.5rem;
		text-align: center;
		&-link {
			display: inline-block;
			height: 20px;
			&-vk {
				width: 35px;
				background: url("../img/social.png") no-repeat 0 0;
			}
			&-fb {
				width: 20px;
				background: url("../img/social.png") no-repeat -45px 0;
			}
			&-mail {
				width: 45px;
				background: url("../img/social.png") no-repeat -73px 0;
			}
		}
	}

	&__config {
		position: absolute;
		top: 2rem;
		left: 2rem;
		width: 36px;
		height: 36px;
		cursor: pointer;
		background: url("../img/config.png") no-repeat;
	}

	&__online {
		position: absolute;
		top: 2rem;
		right: 2rem;
		width: 36px;
		height: 36px;
		background: url("../img/online.png") no-repeat;
		background-size: 100%;
	}
}
