.about {
	display: flex;
	flex-direction: row;
	box-shadow: 0 2px 5px 0 rgba($black, .13);
	z-index: 2;
	&__item {
		padding: 1.6rem 2.4rem;
		flex-basis: 100%;
		position: relative;
		overflow: hidden;
	}

	&__title {
		margin-bottom: 1.4rem;
		text-transform: uppercase;
		color: $darkGrey;
		font-weight: 500;
		font-size: 2.4rem;
	}

	&__descr {
		color: #7b7979;
		font-size: 1.8rem;
	}
}
