.trip-slider {
	padding-top: 2rem;
	text-align: center;
	width: 600px;
	position: relative;

	&__item {
		position: relative;
	}

	&__img {
		display: inline-block !important;
		border-radius: 50%;
		width: 120px;
	}

	&__label {
		position: absolute;
		display: block;
		border-radius: 50%;
		background: $blue;
		color: $white;
		font-size: 1.2rem;
		padding: .5rem;
		top: 1rem;
		right: 2rem;
		&--negative {
			background: $crimson;
		}
	}

	&__title {
		font-size: 2rem;
		color: #7b7979;
		font-weight: 400;
	}
	&__prev,
	&__next {
		// background: #000;
		width: 40px;
		height: 100%;
		position: absolute;
		z-index: 2;
	}

	&__prev {
		left: 0;

	}

	&__next {
		right:	2rem;
	}
}
