.trip-header {
	position: relative;
	min-height: 25.6rem;
	padding: 2rem 2.4rem 7.8rem;
	background: $black;

	&__title {
		text-transform: uppercase;
		color: #f4f4f4;
		font-size: 4.8rem;
		font-weight: 300;
		line-height: 8.8rem;
	}

	&__date {
		position: absolute;
		top: 3rem;
		left: 22rem;
		display: inline-block;
		padding: .5rem 1rem .5rem 0;
		color: #1e1e1e;
		border-radius: 2rem;
		background: #ededed;
		font-size: 1.6rem;

		& > span {
			padding: .5rem 1rem;
			border-radius: 2rem;
			background: $white;
		}
	}

	&__descr {
		width: 40%;
		margin-bottom: 1.5rem;
		color: #ececec;
		font-size: 2.4rem;
		font-weight: 300;
	}

	&__button {
		position: absolute;
		bottom: 2rem;
		padding: 0 2.5rem 0 6.8rem;
		text-align: right;
		text-transform: uppercase;
		border: 1px solid;
		font-size: 2.4rem;
		font-weight: 300;
		line-height: 5rem;

		&--request {
			left: 2rem;
			color: $white;
			background: #39e37d url(../img/checkbox.png) no-repeat 2rem 50%;
		}

		&--settings {
			right: 2rem;
			color: $white;
			background: #eba000 url(../img/settings.png) no-repeat 2rem 50%;
		}
	}
}
