.trip-about {
	&__item {
		position: relative;
		padding: 2rem 2rem 11rem 2rem;
		flex-basis: 100%;
	}
	&__btn {
		padding: 0 1.2rem 0 4.4rem;
		&:before {
			@include btn-triagnle($orange, transparent, transparent, transparent);
			left: 1rem;
			border-width: 8px 7px 0 7px;
			margin-top: -.5rem;
		}
	}
}

.primary-btn {
	position: absolute;
	text-decoration: none;
	right: 2.4rem;
	bottom: 2rem;
	display: block;
	text-transform: uppercase;
	color: $orange;
	border: 2px solid;
	background: none;
	font-size: 1.8rem;
	line-height: 3.6rem;
}
