
.sidebar{
	&__button{
		border-top: 1px solid #1772A6;
	}
}

.setting {
	display: flex;
	height: 12.8rem;
	margin-left: 12.8rem;
	&__return {
		display: flex;
		align-items: center;
		width: 25%;
		background: #efefef;
	}
	a {
		position: relative;
		margin-left: 20%;
		font-size: 32px;
		font-family: "Roboto";
		color: rgb(23, 105, 151);
		text-decoration: none;
		text-transform: uppercase;
		&:active {
			color: #fff;
		}
		&:hover .setting__arrow {
			left: -30%;
			transition: .2s;
			border-color: #000;
		}
	}
	&__arrow {
		position: absolute;
		left: -25%;
		top: 25%;
		transform: rotate(45deg);
		width: 15px;
		height: 15px;
		border-bottom: 3px solid #176997;
		border-left: 3px solid #176997;
	}
	&__main-settings {
		display: flex;
		align-items: center;
		width: 75%;
		background: #176997;
		h1 {
			font-size: 32px;
			font-family: "Roboto";
			color: #fff;
			margin-left: 5%;
			text-transform: uppercase;
		}
	}
}

.content {
	display: flex;
	height: 30vh;
	&__nav {
		display: flex;
		flex-direction: column;
		width: 25%;
		&-item {
			display: flex;
			align-items: center;
			position: relative;
			height: 20%;
			padding-left: 20%;
			font-size: 14px;
			font-family: "Roboto";
			color: #3a3a3a;
			text-decoration: none;
			text-transform: uppercase;
			font-weight: 600;
			&:hover {
				background: #DFDFDF;
				border-top: 1px solid #999;
				border-bottom: 1px solid #999;
			}
			&:active {
				color: #fff;
			}
		}
		&-active {
			border-left: 5px solid #143B51;
			text-indent: -5px;
			color: rgb(23, 105, 151);
			&:hover {
				border-left: 5px solid #143B51;
				background: #fff;
			}
			&:active {
				color: #fff;
			}
		}
		span {
			color: rgb(23, 105, 151);
			border: 2px solid rgb(23, 105, 151);
			padding: 5px 12px;
			border-radius: 5px;
			position: absolute;
			top: 12%;
			right: 5%;
		}
	}
}

.main-content {
	display: flex;
	flex-wrap: wrap;
	align-content: flex-start;
	width: 75%;
	height: calc(100vh - 12.8rem);
	border-left: 3px solid #efefef;
	overflow: auto;
	&__item{
		width: 50%;
		height: 110px;
		background: #fff;
		position: relative;
		h3 {
			font-size: 14px;
			font-family: "Roboto";
			color: rgb(23, 105, 151);
			text-transform: uppercase;
			padding: 35px 0 10px 25px;
		}
		h4{
			@extend h3;
			padding: 10px 0 10px 25px;
		}
		p {
			padding: 0 20px 10px 25px;
			font-size: 13px;
			color: #3a3a3a;
			font-weight: 500;
			a {
				padding: 3px 10px;
				background: #E0E0E0;
				font-size: 12px;
				color: #3a3a3a;
				border-radius: 5px;
				font-weight: 500;
				text-decoration: none;
				text-transform: uppercase;
				&:hover{
					color: rgb(23, 105, 151);
				}
				&:active{
					color: #fff;
				}
			}
		}
		hr {
			border: none;
			background: #999;
			height: 1px;
			margin: 0 25px;
		}
		span {
			float: right;
			margin: 5px 25px 0 0;
			font-size: 10px;
			color: #999;
			text-transform: uppercase;
		}
		strong {
			font-size: 11px;
			margin-left: 10px;
		}
		&-img {
			position: absolute;
			right: 8%;
			bottom: 48%;
		}
	}
	&__rules{
	@extend .main-content__item;
	width: 100%;
	background: red;
	height: calc(100vh - 300px - 12.8rem);
	background: #fff;
	border-top: 6px solid #efefef;
	hr{
		height: 3px;
		background: #EBEBEB;
		}
	}
}

@media screen and (max-width: 1100px){
.main-content__item{
	width: 100%;
	height: 100px;
		h3{
		font-size: 11px;
		padding: 20px 0 10px 25px;
		}
		p{
			font-size: 9px;
		}
		strong{
			font-size: 8px;
		}
	}

.content__nav{
	&-item {
	padding-left: 15%;
	font-size: 11px;
	}
	span{
	padding: 5px 10px;
	top: 20%;
		}
	}
}

@media screen and (max-width: 900px){
.sidebar {
	width: 8rem;
	&__logo{
	height: 8rem;
	font-size: 4.2rem;
	line-height: 8rem;
		}
	&__photo{
		width: 80%;
		height: 80%;
		}
	}

.main-content__item{
	height: 80px;
	h3{
		padding: 10px 0 10px 25px;
	}
	span{
		font-size: 8px;
	}
	&-img{
		bottom: 40%;
	}
}

.setting{
	height: 8rem;
	margin-left: 8rem;
	a{
	font-size: 18px;
		}
	&__return{
			width: 35%;
		}
	&__main-settings{
	h1{
		font-size: 18px;
			}
		}
	&__arrow{
		height: 12px;
		width: 12px;
		}
	}

.content{
	padding-left: 8rem;
	&__nav{
		width: 35%;
		}
	}
}

@media screen and (max-width: 700px){
.content__nav{
	&-item {
	padding-left: 10%;
	font-size: 8px;
		}
		span{
			border: none;
			top: 25%;
		}
	}

.setting{
	a{
	margin-left: 30%
	}
}

.main-content__item{
	height: 70px;
		h3{
		font-size: 8px;
		}
		p{
			font-size: 6px;
		}
		strong{
			font-size: 5px;
		}
		&-img{
			bottom: 40%;
			right: 10%;
		}
	}
}

@media screen and (max-width: 500px){
.sidebar {
	width: 5rem;
	&__logo{
	height: 5rem;
	font-size: 2.2rem;
	line-height: 5rem;
		}
	&__photo{
		width: 70%;
		height: 70%;
		&-wrap{
			height: 9rem;
			}
		}
	&__button{
		height: 5rem;
		background-size: 50%;
		}
	}

.content{
	height: 20vh;
	padding-left: 5rem;
	&__nav{
		width: 40%;
		span{
			top: 20%;
			}
		}
	&__nav-item {
		font-size: 6px;
		}
	}

.setting{
	height: 5rem;
	margin-left: 5rem;
	a{
	font-size: 12px;
	&:hover .setting__arrow{
		left: -50%;
		}
	}
	&__return{
			width: 40%;
		}
	&__main-settings{
	h1{
		font-size: 12px;
			}
		}
	&__arrow{
		top: 15%;
		left: -40%;
		}
	}

.main-content__item{
	span{
		font-size: 4px;
	}
		h3{
		font-size: 8px;
		}
		p{
			font-size: 6px;
			& a{
			font-size: 8px;
			}
		}
		strong{
			font-size: 4px;
		}
		&-img{
			right: 15%;
			height: 10px;
			width: 10px;
		}
	}
}
